<template>
  <el-container>
    <transition name="dis_list">
      <div class="list_box" v-if="listIsDis">
        <transition name="music_alert">
          <span class="music_alert" v-if="musicAlertState">{{musicAlertVal}}</span>
        </transition>
        <div class="list_close" @click="DisList">x</div>
        <div class="music_list">
          <el-container>
          <el-main class="list_l" style="width: 70%">
              <el-tabs v-model="activeName" @tab-click="_getMusicType" type="card">
                <template v-for="item in musicTypeList" >
                <el-tab-pane  :label="item.name" :name="item.id" :class="{type_active:item.id==thisMusicType}">
                  <div class="list_title">
                    <div class="music_boxs">
                    <span style="font-size: 14px;">歌曲列表</span>
                    <img :src="musicStateButton" alt="" class="music_state" @click="MusicStateChange">
                    </div>
                    <div class="music_search_box">
                      <el-input class="music_search"
                                placeholder="搜索歌曲"
                                v-model="musicSearchVal"
                                prefix-icon="el-icon-search"
                                clearable>
                      </el-input>
                      <transition name="music_search">
                        <ul  class="search_list" v-if="musicSearchVal!=''">
                          <li v-for="item in musicSearchList" @click="ListAdd(item)">
                            <span class="music_search_name">{{item.name}}</span>
                            <span class="music_search_ar">{{item.artists[0].name}}</span>
                          </li>
                        </ul>
                      </transition>
                    </div>
                  </div>
                  <div class="music_ul_title">
                    <span>歌曲</span>
                    <span>歌手</span>
                    <span>专辑</span>
                  </div>
                  <ul class="list">
                    <li v-for="(item,index) in thisMusicList" @mouseover="ButtonActive(index)" @dblclick="ListPlay((thisListPage-1)*10+index)">
                      <div class="this_music_shlter" v-if="(thisListPage-1)*10+index==thisMusicIndex"></div>
                      <span>{{item.name}}</span>
                      <span>{{item.ar[0].name}}</span>
                      <span>{{item.al.name}}</span>
                      <transition name="list_button">
                        <div class="music_button" v-if="listButtonActiveIndex==index">
                          <div class="list_play" title="播放这首歌" :style="{backgroundImage:'url('+listPlay+')'}" @click="ListPlay((thisListPage-1)*10+index)"></div>
                          <div class="list_play" title="添加到我的喜欢" :style="{backgroundImage:'url('+add+')'}" @click="ListAdd(item)" v-if="thisMusicType!=-1"></div>
                        </div>
                      </transition>
                    </li>
                  </ul>
                  <div class="list_page" v-if="musicList.length>0">
                    <div class="page_last" v-if="thisListPage!=1"  @click="ListChange(true)"><</div>
                    <div class="page_next"  v-if="thisListPage!=Math.ceil(musicList.length/10)" @click="ListChange(false)">></div>
                  </div>
                </el-tab-pane>
                </template>

              </el-tabs>
          </el-main>
            <el-aside  style="width: 30%">
              <div class="music_talk">
              <img class="music_list_bg" :src="musicImg"/>
              <div class="music_list_shlter" :style="{backgroundImage:'url('+shlter+')'}"></div>
              <ul class="music_talk_list">
                <li v-for="(item,index) in hotTalkList" :key="index">
                  <div class="talk_head">
                    <img :src="item.user.avatarUrl" alt="" class="talk_head_img">
                    <span class="talk_head_name">{{item.user.nickname}}</span>
                  </div>
                  <p class="talk_content">
                    <img class="talk_icon_l" :src="talkicon1"/>
                    {{item.content}}
                    <img class="talk_icon_r" :src="talkicon2"/>
                  </p>
                </li>
              </ul>
              </div>
            </el-aside>
           </el-container>
        </div>
      </div>
    </transition>
    <div class="bbox" :class="{bbox_active:disActive}">
      <div class="pan" :style="{backgroundImage:'url('+pan+')'}" :class="{pan_active:disActive}" @click="DisActive">
        <img :src="musicImg" :onerror="errorImg" alt="" class="pan_c">
      </div>
      <div class="box" :style="{backgroundImage:'url('+musicImg+')'}" :class="{box_active:disActive}" @dblclick="DisList">
        <div class="music_shlter_2" :style="{backgroundImage:'url('+musicImg+')'}" :class="{shlter_active:disActive}"></div>
        <div class="music_shlter" :style="{backgroundImage:'url('+musicImg+')'}" :class="{shlter_active:disActive}"></div>
        <div class="music_shlter_3"></div>
        <div class="music_dis">
          <div class="dis_list" @click="DisList">···</div>
          <p class="music_title">{{musicTitle}}</p>
          <p class="music_intro">歌手: {{musicName}}</p>
          <ul class="music_words">
            <div class="music_words_box" :style="{top:wordsTop+'px'}">
              <li v-for="(item,index) in musicWords" class="music_word" :class="{word_highlight:wordIndex==index}">{{item}}</li>
            </div>
          </ul>
        </div>
        <div class="control_box">
          <div class="control_button">
            <img :src="playIcon" alt="" class="control_icon">
          </div>
          <div class="progress">
            <div class="progress_c" :style="{width:currentProgress}">
              <div class="progress_circle">
                <div class="progress_circle_c"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <audio id="music" ref="audio" name="media" :src="musicUrl"  preload="auto" hidden="true" autoplay controls  muted>
        Your browser does not support the audio element.
      </audio>

      <!--<audio id="music" autoplay="autoplay" :src="musicUrl" name="media"  preload="auto" hidden="true" controls muted>-->
      <!--</audio>-->

       <!--<video id="music" playsinline="playsinline"  x5-video-player-type="h5"-->
              <!--preload="auto" x-webkit-airplay="true"-->
                            <!--x5-video-player-fullscreen="true" autoplay="autoplay" :src="musicUrl" name="media"  x5-playsinline="" x5-video-orientation="portraint" webkit-playsinline="webkit-playsinline"  controls="controls" muted>-->
      <!--<source :src="musicUrl" type="video/mp4">-->
      <!--<source :src="musicUrl" type="video/ogg">-->
                <!--您的浏览器不支持 video 标签。-->
       <!--</video>-->

      <!--<video id="music" autoplay="autoplay" :src="musicUrl" name="media">-->
      <!--</video>-->
    </div>
  </el-container>
</template>

<script>
  import { getWords,getMusicInfo,getMusicUrl,getHotMusic,getSearchSuggest,getHotTalk } from './api/music'
  import pan from './img/pan.png'
  import play from './img/play.png'
  import pause from './img/pause.png'
  import add from './img/add.png'
  import shlter from './img/list_pan.png'
  import listPlay from './img/list_play_hover.png'
  import state0 from './img/state_0.png'
  import state1 from './img/state_1.png'
  import talkicon1 from './img/talkicon1.png'
  import talkicon2 from './img/talkicon2.png'
  import $ from 'jquery'
  export default {
    name:'MusicPlayer',
    data() {
      return {
        o:0,
        top:0,
        pan,
        play,
        pause,
        add,
        shlter,
        listPlay,
        state0,
        state1,
        talkicon1,
        talkicon2,
        playState:true,
        playIcon:pause,
        musicImg:'',
        musicUrl:'',
        musicWords:[],
        musicTitle:'',
        musicName:'',
        wordsTime:[],
        wordsTop:0,
        wordIndex:0,
        currentProgress:'0%',
        musicList:[],
        myMusicList:[],   //存储在本地   可以开始判断有没有 让用户一开始就听这个列表
        thisMusicIndex:1,
        disActive:false,
        listIsDis:false,
        listButtonActiveIndex:-1,
        thisListPage:1,
        activeName: '3778678',
        musicTypeList:[
          {name:'热歌榜',id:'3778678'},
          {name:'新歌榜',id:'3779629'},
          {name:'飙升榜',id:'19723756'},
          {name:'原创歌曲榜',id:'2884035'},
          {name:'我的喜欢',id:'-1'}
        ],
        DataList:{name:'3778678'},
        thisMusicType:-1,
        notPlay:[],
        musicState:0, //0列表循环  1单曲循环
        musicStateButton:state1,
        musicSearchVal:'',
        musicSearchList:[],
        musicAlertVal:'',
        musicAlertState:false,
        musicAlertTimer:'',
        //新增歌词评论
        hotTalkList:[],
        errorImg: 'this.src="https://gitee.com/cnnc/pic-go-picture-bed/raw/master/img/20200921144137.png"'
      }
    },
    mounted() {
      this.Player();
    },
    created() {
      this._getMusicType(this.DataList);
      this.DisAuthorInfo();//禁删~感谢配合
    },
    computed: {
      thisMusicList(){
        return [...this.musicList].splice((this.thisListPage-1)*10,10);  //分页
      },

    },
    watch: {
      musicSearchVal(){
        if(this.musicSearchVal==''){
          this.musicSearchList=[];
        }else{
          getSearchSuggest(this.musicSearchVal).then((res)=>{
            if(res.data.result.songs==undefined){
              this.musicSearchList=[];
            }else{
              this.musicSearchList=res.data.result.songs;
            }
          })
        }
      }
    },
    methods: {
      //禁删~感谢配合
      DisAuthorInfo(){
        console.log("%c音乐播放器1.0v|Author:Jeonke，博客地址：https://itcnw3c.com/", "background-color:rgb(30,30,30);border-radius:4px;font-size:12px;padding:4px;color:rgb(220,208,129);");
      },
      MusicAlert(val){
        this.musicAlertState=true;
        this.musicAlertVal=val;
        clearTimeout(this.musicAlertTimer);
        this.musicAlertTimer=setTimeout(()=>{
          this.musicAlertState=false;
          this.musicAlertVal='';
        },2000);
      },
      ListAdd(obj){
        getMusicInfo(obj.id).then((res)=>{
          this.musicSearchVal='';
          if(this.myMusicList.length==0){
            this.myMusicList=[res.data.songs[0]];
            this._getMusicType(-1);
            //第一次搜索直接播放
          }else{
            this.myMusicList.push(res.data.songs[0]);
            //提示已经添加进去

          }
          this.MusicAlert('添加成功');
        })
      },
      MusicStateChange(){
        if(this.musicState==0){
          this.musicState=1;
          this.musicStateButton=this.state0;
          this.MusicAlert('已切换为单曲循环模式');
        }else{
          this.musicState=0;
          this.musicStateButton=this.state1;
          this.MusicAlert('已切换为列表循环模式');
        }
      },
      DisList(){
        this.listIsDis=this.listIsDis ? false : true;
      },
      ListChange(isLast){
        if(isLast){
          this.thisListPage--;
        }else{
          this.thisListPage++;
        }
      },
      ListPlay(id){
        if(this.thisMusicIndex!=id){
          this.thisMusicIndex=id>this.musicList.length-1 ? 0 : id;
          this._getInfo();
          this.top=0;
          this.o=0;
          this.wordIndex=0;
          this.wordsTop=0;
          this.currentProgress='0%';
          if(!this.playState){
            $('.control_icon').click();
          }
        }
      },
      ButtonActive(id){
        this.listButtonActiveIndex=id;
      },
      DisActive(){
        this.disActive=this.disActive ? false : true;
      },
      _getMusicType(tab, event){
        // console.log(tab, event);
        // console.log(tab.name, event)
        this.musicList=[];
        var id = 0;
        id = tab.name;
        if(this.thisMusicType!=id){
          // console.log("AAA:"+id)
          this.notPlay=[];
          if(id==-1){
            if(this.myMusicList.length!=0){
              this.musicList=this.myMusicList;
              this.thisMusicType=id;
              this.thisMusicIndex=0;
              this.thisListPage=1;
              this._getInfo();
              this.top=0;
              this.o=0;
              this.wordIndex=0;
              this.wordsTop=0;
              this.currentProgress='0%';
              if(!this.playState){
                $('.control_icon').click();
              }
              // console.log("============"+ this.musicList)
            }else{//自定义库没有歌曲 提示需要搜索才可以添加
              // this.MusicAlert('没有歌曲,需要自己添加');
            }

          }else{
            getHotMusic(id).then((res)=>{

              // console.log("BBB:"+id)
              this.musicList=res.data.playlist.tracks.splice(0,200);
              this.thisMusicType=id;
              this.thisMusicIndex=0;
              this.thisListPage=1;
              this._getInfo();
              this.top=0;
              this.o=0;
              this.wordIndex=0;
              this.wordsTop=0;
              this.currentProgress='0%';
              if(!this.playState){
                $('.control_icon').click();
              }
              // console.log("============"+ this.musicList[this.thisMusicIndex].id)
            })
          }
        }
      },
      _getInfo(){
        getMusicUrl(this.musicList[this.thisMusicIndex].id).then((res)=>{
          if(res.data.data[0].url==null || res.data.data[0].url=='' || res.data.data[0].url==undefined){
            if(this.notPlay.length!=this.musicList.length){

              let nextIndex=this.thisMusicIndex+1;
              if(this.notPlay.indexOf(this.thisMusicIndex)==-1){
                this.notPlay.push(this.thisMusicIndex);
              }
              this.MusicAlert(`${this.musicList[this.thisMusicIndex].name}因为一些原因不能播放`);
              this.ListPlay(nextIndex);//寻找下一首歌  直到找到

              //提示这首歌不能放
            }else{
              //遍历完没有找到
              // console.log('not');
              this.MusicAlert('此列表所有歌都不能播放');
            }
          }else{
            this.musicUrl=res.data.data[0].url.replace('http://','https://');
            this.musicImg=this.musicList[this.thisMusicIndex].al.picUrl.replace('http://','https://')+'?param=300y300';
            this.musicTitle=this.musicList[this.thisMusicIndex].name;
            let name_arr=[];
            this.musicList[this.thisMusicIndex].ar.forEach((i)=>{
              name_arr.push(i.name);
            })
            this.musicName=name_arr.join('/');
            getWords(this.musicList[this.thisMusicIndex].id).then((res)=>{
              if(!res.data.nolyric){
                let info=this.Cut(res.data.lrc.lyric);
                this.musicWords=info.wordArr;
                this.wordsTime=info.timeArr;
              }
            })
            getHotTalk(this.musicList[this.thisMusicIndex].id).then((res)=>{
              let count=0;
              this.hotTalkList=res.data.hotComments.splice(0,3);
              this.hotTalkList.forEach(e=>{
                count+=e.content.length;
                e.user.avatarUrl=e.user.avatarUrl+'?param=50y50';
              })
              if(count>=200){
                this.hotTalkList=this.hotTalkList.slice(0,2);
              }
            })
          }
        })
      },
      Ltrim(s){
        return s.replace(/(^\s*)/g, "");
      },
      Rtrim(s){
        return s.replace(/(\s*$)/g, "");
      },
      //歌词截取函数
      Cut(str){
        let timeArr=[];
        let wordArr=[];
        timeArr=str.split('[');
        timeArr.splice(0,1);
        for(let i=0;i<timeArr.length;i++){
          let cut=timeArr[i].split(']');
          let time=cut[0].split('.')[0].split(':');
          timeArr[i]=Number.parseInt(time[0])*60+Number.parseInt(time[1]);
          timeArr[i]=isNaN(timeArr[i]) ? 0 : timeArr[i]; //处理NaN
          wordArr[i]=this.Rtrim(this.Ltrim(cut[1]));
        }
        return {timeArr:timeArr,wordArr:wordArr}
      },
      Player(){
        let self=this;
        // let player=$('#music')[0];
        let player= this.$refs.audio;
        let playerTimer=setInterval(timer,1000);
        //定时器函数
        $('body').on('click',()=>{
          player.currentTime = 0;
          player.muted = false;
          player.play();
          $('body').unbind('click');
        })
        function timer(){
          self.currentProgress=`${(player.currentTime/player.duration)*100}%`
          //接着这里写歌词滚动
          if(player.currentTime>=self.wordsTime[self.o+1]){
            self.top+=Number.parseInt($('.music_word').eq(self.o).height()+Number.parseInt($('.music_word').eq(self.o).css('marginTop')));
            if(self.top>=$('.music_words').height()/2-11){  //开始滚动的高度
              self.wordsTop+=-Number.parseInt($('.music_word').eq(self.o).height()+Number.parseInt($('.music_word').eq(self.o).css('marginTop')));
            }
            self.wordIndex=self.o+1;
            self.o++;
          }
          if(player.currentTime>=player.duration){    //切歌
            if(self.musicList.length!=1){  //只有一首歌  重复播放
              if(self.musicState==0)
              {
                self.thisMusicIndex=self.thisMusicIndex>=self.musicList.length-1 ? 0 : self.thisMusicIndex+1;
                self._getInfo();
              }
            }
            player.muted = false;
            player.play();
            self.top=0;
            self.o=0;
            self.wordIndex=0;
            self.wordsTop=0;
            self.currentProgress='0%';
          }
        }
        //进度条控制
        $('.progress').on('mousedown',(ev)=>{
          console.log()
          let e=ev||event;
          let pro=((e.clientX-$('.progress').offset().left)/$('.progress').width())
          clearInterval(playerTimer);
          this.currentProgress=`${pro*100}%`
          $(document).on('mousemove',(ev)=>{
            let e=ev||event;
            pro=((e.clientX-$('.progress').offset().left)/$('.progress').width())
            this.currentProgress=`${pro*100}%`
          })
          $(document).on('mouseup',()=>{
            player.currentTime=player.duration*pro;
            let c_arr=[...this.wordsTime];
            c_arr.push(player.currentTime);
            c_arr.sort((l,r)=>{
              return l-r
            });
            let now_o=c_arr.indexOf(player.currentTime)-1;
            let diff_h=0;
            if(this.o<now_o){
              for(let i=this.o;i<now_o;i++){
                diff_h+=-Number.parseInt($('.music_word').eq(i).height()+Number.parseInt($('.music_word').eq(i).css('marginTop')))
              }
            }else{
              for(let i=now_o;i<this.o;i++){
                diff_h+=Number.parseInt($('.music_word').eq(i).height()+Number.parseInt($('.music_word').eq(i).css('marginTop')))
              }
            }
            this.wordsTop+=diff_h;
            self.wordIndex=this.o=now_o;
            clearInterval(playerTimer);
            playerTimer=setInterval(timer,1000);
            this.playState=true;
            this.playIcon=this.pause;
            if(player.currentTime>=player.duration){
              this.top=0;
              this.o=0;
              this.wordIndex=0;
              this.wordsTop=0;
              this.currentProgress='0%';
            }
            player.muted = false;
            player.play();
            $(document).unbind('mousemove');
            $(document).unbind('mouseup');
          })
        })
        //播放暂停按钮控制
        $('.control_icon').on('click',()=>{
          if(this.playState){
            player.pause();
            this.playState=false;
            this.playIcon=this.play;
            clearInterval(playerTimer);
          }else{
            player.muted = false;
            player.play();
            this.playState=true;
            this.playIcon=this.pause;
            clearInterval(playerTimer);
            playerTimer=setInterval(timer,1000);
          }
        });
      },
      Contorl(){
        let player = this.$refs.audio;
        // let player = $('#music')[0];
        player.currentTime=100;
      }
    },
  }
</script>
<!--播放器样式-->
<style scoped>
  * {
    padding: 0px;
    margin: 0px;
    text-decoration: none;
    font-family: Consolas, '微软雅黑';
    box-sizing: border-box;
    word-bteak:break-all;
  }

  #music {
    width: 0px;
    height: 0px;
  }



  .bbox {
    width: 100px;
    height: 100px;
    border-radius: 5px;
    position: fixed;
    z-index: 500;
    bottom: 150px;
    right: 30px;
    transition: all .5s;
  }

  .bbox_active {
    width: 300px;
    height: 300px;
  }

  .box {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-size: 100% 100%;
    border-radius: 5px;
    transition: all .5s;
    opacity: 0;
  }

  .box-enter, .box-leave-to {
    opacity: 0;
  }

  .music_shlter {
    width: 100%;
    height: 100%;
    position: absolute;
    filter: blur(10px);
    background-size: 100% 100%;
    transition: all .5s;
    z-index: 99;
    opacity: 0;
  }

  .music_shlter_2 {
    width: 100%;
    height: 100%;
    position: absolute;
    filter: blur(5px);
    background-size: 100% 100%;
    transition: all .5s;
    opacity: 0;
  }

  .music_shlter_3 {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.1);
    transition: all .5s;
    border-radius: 5px;
    filter: blur(2px);
  }

  .music_dis {
    width: 100%;
    height: 240px;
    position: relative;
  }

  .dis_list {
    position: absolute;
    right: 10px;
    top: 10px;
    width: 40px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    cursor: pointer;
    font-size: 12px;
    color: white;
  }

  .pan {
    width: 100px;
    height: 100px;
    background-size: 100% 100%;
    position: absolute;
    left: 0px;
    top: 0px;
    padding: 16px;
    animation: pan 10s linear infinite;
    z-index: 103;
    transition: all .5s;
    cursor: pointer;
    border-radius: 100%;
  }

  .pan:hover {
    box-shadow: 0px 0px 10px rgb(100, 100, 100);
    opacity: 1;
  }

  .pan_c {
    width: 100%;
    height: 100%;
    border-radius: 100%;
  }

  .music_title {
    width: 100%;
    height: 40px;
    text-align: center;
    color: white;
    line-height: 60px;
    font-size: 15px;
  }

  .music_intro {
    width: 100%;
    height: 40px;
    text-align: center;
    color: rgba(255, 255, 255, 0.6);
    line-height: 30px;
    font-size: 12px;
  }

  .music_words_box {
    width: 100%;
    height: auto;
    position: absolute;
    top: 0px;
    transition: all .5s;
  }

  .music_words {
    width: 240px;
    height: 160px;
    margin: auto;
    color: white;
    text-align: center;
    overflow: hidden;
    position: relative;
  }


  .music_words li {
    list-style: none;
    font-size: 12px;
    margin-top: 5px;
    min-height: 18px;
  }

  .control_box {
    width: 90%;
    height: 60px;
    position: relative;
    margin: auto;
  }

  .control_button {
    width: 14px;
    height: 14px;
    position: absolute;
    left: 20px;
    bottom: 15px;

  }

  .control_icon {
    height: 100%;
    cursor: pointer;
  }

  .progress {
    width: 150px;
    height: 4px;
    border-radius: 10px;
    background-color: rgb(194, 194, 196);
    cursor: pointer;
    margin-top: 18px;
    position: absolute;
    left: 0px;
    right: 0px;
    margin: auto;
    bottom: 20px;
  }

  .progress_c {
    width: 50%;
    height: 100%;
    /*background-color: rgb(232, 60, 60);*/
    background-color:  #02ad02e3;
    transition: all .2s;
    position: relative;
    border-radius: 10px;
    max-width: 100%;
  }

  .progress_circle {
    position: absolute;
    width: 15px;
    height: 15px;
    right: -7.5px;
    top: -5.5px;
    background-color: white;
    border: 1px solid rgb(220, 220, 220);
    border-radius: 100%;
  }

  .progress_circle_c {
    width: 5px;
    height: 5px;
    position: absolute;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    margin: auto;
    /*background-color: rgb(232, 60, 60);*/
    background-color:  #02ad02e3;
    border-radius: 100%;
  }

  .word_highlight {
    /*color: white;*/
    font-weight: bold;
    background:orange -webkit-linear-gradient(left, rgba(6, 199, 253, 0.88), rgba(6, 199, 253, 0.88)) no-repeat 0 0;
    -webkit-text-fill-color:transparent;
    -webkit-background-clip:text;
    background-size:100% 100%;
    animation: scan 5s linear;
  }
  @keyframes scan {
    0% {
      background-size:0 100%;
    }
    100% {
      background-size:100% 100%;
    }
  }

  .pan_active {
    width: 40px;
    height: 40px;
    background-size: 100% 100%;
    position: absolute;
    left: 10px;
    top: 10px;
    padding: 6px;
    opacity: 0.8;
    z-index: 103;
  }

  .box_active {
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
    border-radius: 5px;
    overflow: unset;
    /* older safari/Chrome browsers */
    -webkit-opacity: 1;
    /* Netscape and Older than Firefox 0.9 */
    -moz-opacity: 1;
    /* Safari 1.x (pre WebKit!) 老式khtml内核的Safari浏览器*/
    -khtml-opacity: 1;
    /* IE9 + etc...modern browsers */
    opacity: 1;
    /* IE 4-9 */
    filter: alpha(opacity=100);
    /*This works in IE 8 & 9 too*/
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
    /*IE4-IE9*/
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=100);
  }

  .shlter_active {
    opacity: 1;
    z-index: unset;
  }

  .dis_list-enter, .dis_list-leave-to {
    opacity: 0;
  }

  .list_box {
    width: 100%;
    height: 100%;
    position: fixed;
    margin: 0 auto;
    background: -webkit-radial-gradient(#353138 0%, #1a181c 100%);
    z-index: 499;
    transition: all .5s;
  }

  .music_alert-enter, .music_alert-leave-to {
    transform: translateY(-30px);
    opacity: 0;
  }

  .music_alert {
    position: absolute;
    top: 30px;
    left: 0px;
    right: 0px;
    margin: auto;
    padding: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    width: 200px;
    height: auto;
    border-radius: 2px;
    background-color: white;
    text-align: center;
    transition: all .5s;
    color: #f9040b;
    font-size: 12px;
    z-index: 600;
  }

  .list_close {
    position: absolute;
    right: 20px; /*top: 20px;*/
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    color: rgba(225, 225, 225, 0.9);
    cursor: pointer;
    font-size: 20px;
    z-index: 600;
  }

  .music_list {
    /*width: 1320px;*/
    /*height: 840px;*/
    position: relative;
    /*left: 0px;*/
    /*right: 0px;*/
    /*bottom: 0px;*/
    /*top: 0px;*/
    margin: 0 auto;
    border-radius: 5px;
  }

  .list_title {
    width: 100%;
    height: 40px;
    color: white;
    position: relative;
  }

  .music_state {
    width: 15px;
    /*margin-left: 40px;*/
    cursor: pointer;
  }

  .music_boxs{
    position: absolute;
    z-index: 999999;
  }
  .music_search_box {
    position: absolute;
    right: 0px;
    top: 0px;
    width: 250px;
    height: 35px;
    z-index: 999999;
  }

  .music_search {
    width: 100%;
    height: 100%;
    vertical-align: top;
    outline: 0;
    box-shadow: none;
    border-radius: 4px 4px 4px 4px;
    overflow: hidden;
  }
  .music_search >>>.el-input__inner{
    font: 16px/18px arial;
    font-size: 18px!important;
    color:#222!important;
    background: #fff;
    border: 0px solid #c4c7ce;/*no*/
  }

  .search_list {
    font: 16px/18px arial;
    width: 100%;
    height: auto;
    position: absolute;
    top: 36px;
    background-color: rgba(255, 255, 255, 255);
    border-radius: 2px;
    transition: all .5s;
    z-index: 999999;
    overflow: hidden;
  }

  .search_list li {
    font: 16px/18px arial;
    padding: 8px;
    width: 100%;
    height: 35px;
    line-height: 25px;
    color: rgba(26, 24, 28, 0.8);
    padding-left: 10px;
    padding-right: 10px;
    cursor: pointer;
    transition: all .5s;
    z-index: 999999;
  }

  .search_list li:hover {
    background-color: rgba(200, 200, 200,200);
    z-index: 999999;
  }

  .music_search_name {
    font: 16px/18px arial;
    width: 55%;
    float: left;
    height: 100%;
    /*overflow: hidden;*/
  }

  .music_search_ar {
    font: 16px/18px arial;
    margin-left: 5%;
    width: 40%;
    float: left;
    height: 100%;
    /*overflow: hidden;*/
  }

  .music_search-enter-active,
  .music_search-enter, .music_search-leave-to {
    opacity: 0;
  }

  .list {
    width: 100%;
    height: auto;
    list-style: none;
    background-color: #131212;
    overflow:hidden;
    zoom:1;
  }

  .music_ul_title {
    /*width: 135px;*/
    height: 40px;
    line-height: 40px;
    border-radius: 2px;
    border: 0px;
    background-color: rgba(0, 0, 0, 0.97);
  }

  .list li {
    width: 100%;
    height: 60px;
    line-height: 60px;
    border-bottom: 1px solid rgba(150, 150, 150, 0.1);
    transition: all .5s;
    position: relative;
  }

  .this_music_shlter {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: -1;
    background-color: rgba(26, 24, 28, 0.5);
  }

  .list li:hover {
    /*background-color: rgba(26, 24, 28, 0.3);*/
    background-color: #131212;
  }

  .list li span, .music_ul_title span {
    width: 30%;
    height: 100%;
    color: rgba(225, 225, 225, 0.8);
    font-size: 12px;
    font-weight: 200;
    text-align: left;/*文字靠左居中*/
    text-indent: 20px;/*缩进*/
    /*float: left;*/
    overflow: hidden;
    white-space: nowrap;  /*强制span不换行*/
    display: inline-block;  /*将span当做块级元素对待*/
    text-overflow: ellipsis;  /*超出部分以点号代替*/
  }

  .music_button {
    width: 34%;
    /*height: 100%;*/
    position: absolute;
    right: 0px;
    top: 24%;
    bottom: 0px;
    transition: all .5s;
    align-items: center;
    z-index: 600;
    /*margin-left: 20px;*/
    background-color: #131212;
  }

  .list_play {
    width: 28px;
    height: 28px;
    background-size: 100% 100%;
    align-items: center;

    /*margin-top: 16px;*/
    cursor: pointer;
    opacity: 2;
    transition: all .5s;
    float: left;
    margin-left: 10px;
    background-color: #131212;
  }

  .list_play:first-child {
    margin-left: 0px;
  }

  .list_play:hover{
    opacity: 1;
  }

  .list_button-enter, .list_button-leave-to {
    opacity: 0;
  }

  .list_page {
    width: 100%;
    height: 40px;
    position: relative;
    background-color: rgba(0, 0, 0, 0.97);
  }

  .page_last {
    position: absolute;
    left: 0px;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    font-size: 15px;
    cursor: pointer;
    color: white;
    -moz-user-select: none; /*火狐*/
    -webkit-user-select: none; /*webkit浏览器*/
    -ms-user-select: none; /*IE10*/
    user-select: none;
  }

  .page_next {
    position: absolute;
    right: 0px;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    font-size: 15px;
    cursor: pointer;
    color: white;
    -moz-user-select: none; /*火狐*/
    -webkit-user-select: none; /*webkit浏览器*/
    -ms-user-select: none; /*IE10*/
    user-select: none;
  }

  .music_list_bg {
    width: 170px;
    display: block;
    /*margin-left: 25px;*/
    margin-top: 90px;
    -moz-user-select: none; /*火狐*/
    -webkit-user-select: none; /*webkit浏览器*/
    -ms-user-select: none; /*IE10*/
    user-select: none;

  }

  .music_list_shlter {
    width: 200px;
    height: 170px;
    background-size: 100% 100%;
    position: absolute;
    top: 90px;
    /*left: 25px;*/
  }

  .music_talk{
    /*width: 100%;*/
    /*height: 620px;*/
    overflow:auto;
    zoom:1;
  }

  .music_talk_list {
    /*width: 250px;*/
    height: auto;
    text-align: left;
    margin-top: 20px;
    list-style: none;
  }

  .music_talk_list li {
    width: 100%;
    height: auto;
    margin-top: 10px;
  }

  .talk_head {
    width: 100%;
    height: 50px;
    position: relative;
  }

  .talk_head_img {
    width: 25px;
    height: 25px;
    float: left;
    border-radius: 100%;
    margin-top: 12.5px;
  }

  .talk_head_name {
    float: left;
    height: 50px;
    line-height: 50px;
    color: rgba(225, 225, 225, 1);
    margin-left: 10px;
  }

  .talk_content {
    width: 100%;
    height: auto;
    color: rgba(225, 225, 225, 0.8);
    box-sizing: border-box;
    padding: 15px;
    position: relative;
  }

  .talk_icon_l {
    position: absolute;
    width: 10px;
    height: 10px;
    left: 0px;
    top: 0px;
  }

  .talk_icon_r {
    position: absolute;
    width: 10px;
    height: 10px;
    right: 0px;
    bottom: 0px;
  }

  .music_type {
    width: 120px;
    height: auto;
    overflow: hidden;
    list-style: none;
    position: absolute;
    left: -120px;
    top: 90px;
  }

  .music_type li {
    width: 100%;
    height: 35px;
    border: 1px solid rgba(225, 225, 225, 0.6);
    color: rgba(225, 225, 225, 0.6);
    line-height: 35px;
    text-align: center;
    box-sizing: border-box;
    border-radius: 3px;
    margin-top: 20px;
    cursor: pointer;
    transition: all .5s;
    font-size: 12px;
  }

  .music_type li:first-child {
    margin-top: 0px;
  }

  .music_type li:hover {
    border: 1px solid rgba(225, 225, 225, 1);
    color: rgba(225, 225, 225, 1);
  }

  .type_active {
    /*border: 1px solid rgba(225, 225, 225, 1) !important;*/
    color: rgba(225, 225, 225, 1) !important;
  }

  @keyframes pan {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

</style>
<style lang="scss" scoped>
  .list_l {
    width:70%;
    height: 100%;
    /*float: left;*/
    /*padding: 50px;*/
    /*margin-left: 120px;*/
    box-sizing: border-box;
    position: relative;
   >>>.el-tabs__item.is-active {
      color: #ffac14;
    }
    >>> .el-tabs__item {
      padding: 0 0.266667rem;
      height: 0.533333rem;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      line-height: 0.533333rem;
      display: inline-block;
      list-style: none;
      font-size: 0.186667rem;
      font-weight: 500;
      color: #fff;
    }
    >>> .el-tabs__item:hover {
      color: #ffac14;
      cursor: pointer;
    }
    >>> .el-tabs--card .el-tabs__header .el-tabs__nav {
      border: none;
    }
   >>> .el-tabs__header {
      margin: 0;
    }
    >>>.el-tabs__content {
      background-color: transparent;
      padding: 20px;
    }
    >>> .el-tabs--card .el-tabs__header {
      border-bottom: 0px solid transparent;
    }
    >>> .el-tabs--card .el-tabs__header .el-tabs__item.is-active {
      border-bottom: 0px solid transparent;
      background: #fff;
    }
    >>> .el-tabs--card .el-tabs__header .el-tabs__item {
      border-bottom: 0px solid transparent;
      border-left: 1px solid #a3a7af;
      /*border-top-left-radius: 20px;*/
      /*border-top-right-radius: 20px;*/
      &:first-child {
        border-left: 1px solid transparent;
      }
    }
    >>> .el-tabs__item:focus.is-active.is-focus:not(:active) {
      box-shadow: none;
      border-top-left-radius: 20px;
      border-top-right-radius: 20px;
    }
  }
</style>




