<template>
  <div id="app">
    <Player />
    <span id="h">
      <i>小蜗ꦿེ</i><img style="width:60px;height:60px;" :src="backgourndStyle" :onerror="errorImg" /><i id="kcles">๑҉</i>
    </span>
    <router-view />
  </div>
</template>
<script>
import Player from "@/components/player/MusicPlayer";
export default {
  name: "App",
  components: {
    Player,
  },
  data: () => ({
    errorImg:
      'this.src="https://gitee.com/cnnc/pic-go-picture-bed/raw/master/img/avatar.gif"',
  }),
  computed: {
    backgourndStyle: function () {
      let imgName = Math.floor(Math.random() * 26); //进行计算随机
      if (imgName < 10) {
        imgName = "0" + imgName;
      }
      let style =
        "https://gitee.com/cnnc/pic-go-picture-bed/raw/master/img/Sitich/Sitich" +
        imgName +
        ".gif";
      return style;
    },
  },
};
</script>
<style type="text/css">
html {
  height: 100%;
}

body {
  /*background: url('./assets/bg.gif') no-repeat;*/
  /*background-size: 100% 100%;*/
  margin: 0;
  min-height: 100%;
  background:
radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.15) 30%, rgba(255,255,255,.3) 32%, rgba(255,255,255,0) 33%) 0 0,
radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.1) 11%, rgba(255,255,255,.3) 13%, rgba(255,255,255,0) 14%) 0 0,
radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.2) 17%, rgba(255,255,255,.43) 19%, rgba(255,255,255,0) 20%) 0 110px,
radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.2) 11%, rgba(255,255,255,.4) 13%, rgba(255,255,255,0) 14%) -130px -170px,
radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.2) 11%, rgba(255,255,255,.4) 13%, rgba(255,255,255,0) 14%) 130px 370px,
radial-gradient(rgba(255,255,255,0) 0, rgba(255,255,255,.1) 11%, rgba(255,255,255,.2) 13%, rgba(255,255,255,0) 14%) 0 0,
linear-gradient(45deg, #343702 0%, #184500 20%, #187546 30%, #006782 40%, #0b1284 50%, #760ea1 60%, #83096e 70%, #840b2a 80%, #b13e12 90%, #e27412 100%);
background-size: 470px 470px, 970px 970px, 410px 410px, 610px 610px, 530px 530px, 730px 730px, 100% 100%;
background-color: #840b2a;
  background-image:url("https://gitee.com/cnnc/pic-go-picture-bed/raw/master/img/bg.70f2090.gif");
  background-attachment: fixed !important;
  /* background-color:black;
background-image:
radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 40px),
radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 30px),
radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 40px),
radial-gradient(rgba(255,255,255,.4), rgba(255,255,255,.1) 2px, transparent 30px);
background-size: 550px 550px, 350px 350px, 250px 250px, 150px 150px;
background-position: 0 0, 40px 60px, 130px 270px, 70px 100px; */

}

#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#h {
  font-family: 隶书; /*px*/
  font-size: 170px; /*px*/
  font-weight: bold;
  display: block;
  margin: 0 auto;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-repeat: no-repeat;
  left: calc(50% - 272px / 2);
  /*color: #53ff00;*/
  /* color: #f0f8ffe3; */
  -webkit-text-fill-color:transparent;
  background: linear-gradient(to right,rgb(255, 230, 0),hsl(44, 100%, 50%));
  background-image:-webkit-linear-gradient(bottom,hsl(4, 100%, 50%),hsl(44, 100%, 50%),rgb(255, 230, 0));
  background-clip: text;
  -webkit-background-clip: text;

}

#h i {
  font-family: 隶书; /*px*/
  font-style: normal;
}


#kcles{
  -webkit-text-fill-color:transparent;
  background: linear-gradient(to right,rgb(255, 230, 0),hsl(44, 100%, 50%));
  background-image:-webkit-linear-gradient(bottom,hsl(4, 100%, 50%),hsl(44, 100%, 50%),rgb(255, 230, 0));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-animation: hue 60s infinite linear;
}

@-webkit-keyframes hue {
    from {
      -webkit-filter: hue-rotate(0deg);
    }
    to {
      -webkit-filter: hue-rotate(-360deg);
    }
  }
</style>



